import { Link, useLocation } from 'react-router-dom';
import './Header.css';

const Header = () => {
  const location = useLocation();
  
  return (
    <header className="header">
      <div className="header-container">
        <Link to="/" className="logo">
           MovieBox
        </Link>
        
        <nav className="nav">
          <Link 
            to="/" 
            className={`nav-link ${location.pathname === '/' ? 'active' : ''}`}
          >
            
          </Link>
          <Link 
            to="/booking" 
            className={`nav-link ${location.pathname === '/booking' ? 'active' : ''}`}
          >
            
          </Link>
          <Link 
            to="/cinema" 
            className={`nav-link ${location.pathname === '/cinema' ? 'active' : ''}`}
          >
            
          </Link>
          <Link 
            to="/events" 
            className={`nav-link ${location.pathname === '/events' ? 'active' : ''}`}
          >
            
          </Link>
          <Link 
            to="/store" 
            className={`nav-link ${location.pathname === '/store' ? 'active' : ''}`}
          >
            
          </Link>
          <Link 
            to="/benefits" 
            className={`nav-link ${location.pathname === '/benefits' ? 'active' : ''}`}
          >
            
          </Link>
        </nav>
        
        <div className="header-actions">
          <button className="action-button login-btn"></button>
          <button className="action-button signup-btn"></button>
          <button className="action-button quick-booking"></button>
          <div className="header-icons">
            <button className="icon-button"></button>
            <button className="icon-button"></button>
          </div>
        </div>
      </div>
    </header>
  );
};

export default Header;
